<template>
  <div class="flag-left flag-container">
    <img :src="flag" :class="flowC">
  </div>
</template>

<script>
import sampleSize from 'lodash/sampleSize'

export default {
  name: 'Flag',
  data() {
    return {
      selectedFlag: "",
    }
  },
  props: {
    flags: Array,
    flow: String
  },
  methods: {
    randomSelect: function () {
      this.selectedFlag = sampleSize(this.flags, 1)[0]
    }
  },
  computed: {
    flag: function() {
      return "../../../../../static/" + this.selectedFlag + ".png"
    },
    flowC: function () {
      return {
        'flag': true,
        'img-fluid': true,
        'change-up': this.$appStatus('beforeBet') && this.flow == 'up',
        'change-down': this.$appStatus('beforeBet') && this.flow == 'down'
      }
    }
  },
  created() {
    this.randomSelect()
    if (this.$appStatus('beforeBet')) {
      setInterval(() => this.randomSelect(), 5000)
    }
  }
}
</script>

<style scoped>
.flag {
  /* width: 260px;
  height: 180px; */
  opacity: 1;
  box-shadow: 0 0 15px #555;
  border-radius: 3px;
}

.change-up {
  animation: changeUp 5s ease-in;
  animation-iteration-count: infinite;
}

.change-down {
  animation: changeDown 5s ease-in;
  animation-iteration-count: infinite;
}

@keyframes changeUp {
  0% {opacity: 0;}
  5% {opacity: 0; transform: translateY(30px);}
  15% {opacity: 1; transform: translateY(0px);}
  85% {opacity: 1;transform: translateY(0px);}
  95% {opacity: 0;transform: translateY(-30px);}
  100% {opacity: 0;}
}

@keyframes changeDown {
  0% {opacity: 0;}
  5% {opacity: 0; transform: translateY(-30px);}
  15% {opacity: 1; transform: translateY(0px);}
  85% {opacity: 1;transform: translateY(0px);}
  95% {opacity: 0;transform: translateY(30px);}
  100% {opacity: 0;}
}

.flag-container {
  text-align: center;
  padding: 10px;
}
</style>



