import AbiParser from '../../services/abiParser'
import Blockchain from '../../services/Blockchain'

export default class Client {
  constructor() {
    this.parser = new AbiParser()
    this.blockchain = new Blockchain()
  }

  credentials(value) {
    return { from: web3.eth.defaultAccount, value: web3.toWei(value, "ether"), gas: 4712388, gasPrice: 100000000000 }
  }

  getMyAddress() {
    try {
      return web3.eth.defaultAccount;
    } catch (e) {
      return "0x0";
    }
  }

  getNetwork() {
    return new Promise(function(resolve, reject) {
      web3.version.getNetwork((err, netId) => {
        if (!err) { resolve(netId) } else { reject(err) }
      })
    })
  }

  getMyBalance() {
    return new Promise(function(resolve, reject) {
      web3.eth.getBalance(web3.eth.defaultAccount, (err, amount) => resolve(web3.fromWei(amount, "ether").toNumber()))
    });
  }

  totalPotForHomeTeam() {
    return this.blockchain.match()
      .then(i => i.totalPotHomeTeam(this.credentials()))
  }

  totalPotForAwayTeam() {
    return this.blockchain.match()
      .then(i => i.totalPotAwayTeam(this.credentials()))
  }

  bet(bool, amount) {
    return this.blockchain.match()
      .then(i => i.bet(bool, this.credentials(amount)))
  }

  withdrawWinnings() {
    return this.blockchain.match()
      .then(i => i.withdrawWinnings(this.credentials()))
  }

  listenToBetReport(callback) {
    return this.blockchain.match().then(i => i.successfulBet({}, {}).watch((err, e) => callback(e)))
  }

}



