import Vue from 'vue'

export default class StorePlugin {

  static STORE = new Vue({
    data: {
      account: {
        address: 0x0,
        balance: 0
      },
      match: {
        homeTeam: '',
        awayTeam: '',
        totalPotForHomeTeam: 0,
        totalPotForAwayTeam: 0
      },
      pending: {
        home: false,
        away: false
      }
    },
    methods: {
      getMyAddress: function() { return this.account.address },
      getMyBalance: function() { return this.account.balance },
      getHomeTeam: function() { return this.match.homeTeam },
      getAwayTeam: function() { return this.match.awayTeam },
      getTotalPotForHomeTeam: function() { return this.match.totalPotForHomeTeam },
      getTotalPotForAwayTeam: function() { return this.match.totalPotForAwayTeam },
      hasPendingHome: function() { return this.pending.home },
      hasPendingAway: function() { return this.pending.away }
    }
  })

  static install(Vue, options) {
   Vue.prototype.$STORE = StorePlugin.STORE
  }

}



