<template>
  <div class="container">
    <h4 :class="boundP">
      <span :class="boundSpan">{{ days }}</span>&nbsp;&nbsp;days&nbsp;&nbsp;
      <span :class="boundSpan">{{ hours }}</span>&nbsp;&nbsp;hours&nbsp;&nbsp;
      <span :class="boundSpan">{{ minutes }}</span>&nbsp;&nbsp;minutes and&nbsp;&nbsp;
      <span :class="boundSpan">{{ seconds }}</span>&nbsp;&nbsp;seconds to start...
    </h4>
  </div>
</template>

<script>
export default {
    name: "Countdown",
    data() {
      return {
        lastSemifinalsDate: new Date("Jul 12, 2018 00:00:00").getTime(),
        finalsDate: new Date("Jul 15, 2018 21:00:00").getTime(),
        withdrawStartDate: new Date("Jul 15, 2018 23:59:00").getTime(),
        selfDestructDate: new Date("Aug 15, 2018 00:00:00").getTime(),
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0
      }
    },
    methods: {
      calculateDistance: function() {
        let now = new Date().getTime()
        let date = this.$appStatus('beforeBet') ?
          this.lastSemifinalsDate : this.$appStatus('betPeriod') ?
          this.finalsDate : this.$appStatus('duringMatch') ?
          this.withdrawStartDate : this.selfDestructDate
        let difference = date - now
        this.days = Math.floor(difference / (1000 * 60 * 60 * 24));
        this.hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        this.minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
        this.seconds = Math.floor((difference % (1000 * 60)) / 1000);
      }
    },
    computed: {
      boundSpan: function () {
        return {
          'larger': true
        }
      },
      boundP: function () {
        return {
          'countdown': true,
          'text-warning': true
        }
      }
    },
    created() {
      let welcomeMsg = this.$appStatus('betPeriod') ? "Betting starts soon" : "Final starts soon"
      this.calculateDistance()
      this.$addMsg(welcomeMsg)
      setInterval(() => this.calculateDistance(), 1000)
    }
}
</script>

<style scoped>
.countdown {
  text-align: center;
  opacity: 0.5;
  margin-top: 50px;
}

.larger {
  font-weight: bold;
  font-size: 2rem;
}

.help {
  text-align: center;
  font-style: italic;
}
</style>



