<template>
  <div id="root">
    <div id="background" :class="background"></div>

    <div id="modal-body" :class="container">
      <Math v-if="app === 'Math'"></Math>
    </div>
  </div>
</template>

<script>
import Math from './miniapps/math/Root'

export default {
  name: 'ModalRoot',
  data() {
    return {
      selected: null,
      isClosing: false
    }
  },
  props: {
    app: String
  },
  methods: {
  },
  components: {
    Math
  },
  computed: {
    background() {
      return {
        'background-appear': !this.isClosing,
        'background-disappear': this.isClosing
      }
    },
    container() {
      return {
        'container-appear': !this.isClosing,
        'container-disappear': this.isClosing
      }
    },

  },
  created() {
    this.$ACTION.$on("App closed", app => this.isClosing = true)
  }
}
</script>

<style scoped>
#root {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
}

#modal-body {
  position: absolute;
  left: 25%;
  width: 50%;
  top: 10vh;
  height: 70vh;
  background-color: white;
  z-index: 102;
  padding: 20px;
  border-radius: 3px;
  box-shadow: 0 0 15px #555;
}

@keyframes flyaway {
  0% { top: 10vh; opacity: 0.85; }
  100% { top: -30vh; opacity: 0; }
}

@keyframes flyin {
  0% { top: -30vh; opacity: 0.5; }
  100% { top: 10vh; opacity: 1; }
}

.container-appear {
  animation: flyin 0.45s ease-out forwards;
}

.container-disappear {
  animation: flyaway 0.45s ease-out forwards;
}

#background {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 101;
}

.background-appear {
  animation: appear 0.45s ease-out forwards;
}

.background-disappear {
  animation: disappear 0.45s ease-out forwards;
}

@keyframes appear {
  0% { opacity: 0; }
  100% { opacity: 0.25; }
}

@keyframes disappear {
  0% { opacity: 0.25; }
  100% { opacity: 0; }
}
</style>



