<template>
  <div>
    <div id="modal-header">
      <div id="modal-title">
        <h4>{{ title }}</h4>
      </div>
      <div>
        <button class="close-button" @click="$closeApp()">
          <font-awesome-icon icon="share-square"></font-awesome-icon>
        </button>
      </div>
    </div>
    <hr>
  </div>
</template>

<script>
export default {
    name: "ModalHeader",
    props: {
      title: String
    }
}
</script>

<style scoped>
#modal-header {
  display: flex;
  justify-content: space-between;
  padding: 15px 15px 0;
}

#modal-title {
  color: #555;
}

  .close-button {
    border: 0;
    cursor: pointer;
    color: #555;
  }

.close-button:active {
  outline: 0;
}

.close-button:focus {
  outline: 0;
}
</style>



