import Vue from 'vue'
import BootstrapVue from "bootstrap-vue"
import Root from './components/Root'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import ControllerPlugin from './plugins/controller/plugin'
import StorePlugin from './plugins/store/plugin'
import Web3Plugin from './plugins/web3/plugin'

import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import fontawesome from '@fortawesome/fontawesome'
import faCommentSlash from '@fortawesome/fontawesome-free-solid/faCommentSlash'
import faInfoCircle from '@fortawesome/fontawesome-free-solid/faInfoCircle'
import faDoorOpen from '@fortawesome/fontawesome-free-solid/faDoorOpen'
import faHandHoldingUsd from '@fortawesome/fontawesome-free-solid/faHandHoldingUsd'
import faUserSecret from '@fortawesome/fontawesome-free-solid/faUserSecret'
import faShareSquare from '@fortawesome/fontawesome-free-solid/faShareSquare'
import faShieldAlt from '@fortawesome/fontawesome-free-solid/faShieldAlt'
import faPlay from '@fortawesome/fontawesome-free-solid/faPlay'
import faTv from '@fortawesome/fontawesome-free-solid/faTv'
import faSearch from '@fortawesome/fontawesome-free-solid/faSearch'
import faPiggyBank from '@fortawesome/fontawesome-free-solid/faPiggyBank'
import faAngleDoubleDown from '@fortawesome/fontawesome-free-solid/faAngleDoubleDown'
import faGlobe from '@fortawesome/fontawesome-free-solid/faGlobe'
import faUniversity from '@fortawesome/fontawesome-free-solid/faUniversity'
import faHandsHelping from '@fortawesome/fontawesome-free-solid/faHandsHelping'
import faCodeBranch from '@fortawesome/fontawesome-free-solid/faCodeBranch'
import faMoneyCheckAlt from '@fortawesome/fontawesome-free-solid/faMoneyCheckAlt'
import faSignInAlt from '@fortawesome/fontawesome-free-solid/faSignInAlt'
import faSignOutAlt from '@fortawesome/fontawesome-free-solid/faSignOutAlt'
import faSyncAlt from '@fortawesome/fontawesome-free-solid/faSyncAlt'
import faSpinner from '@fortawesome/fontawesome-free-solid/faSpinner'
fontawesome.library.add(faCommentSlash, faSpinner, faTv, faInfoCircle, faSearch, faPiggyBank, faShareSquare, faUniversity, faHandHoldingUsd, faDoorOpen, faUserSecret, faSyncAlt, faShieldAlt, faSignInAlt, faSignOutAlt, faMoneyCheckAlt, faPlay, faHandsHelping, faGlobe, faAngleDoubleDown, faCodeBranch)

Vue.use(ControllerPlugin)
Vue.use(StorePlugin)
Vue.use(Web3Plugin)
Vue.use(BootstrapVue)

Vue.component(FontAwesomeIcon.name, FontAwesomeIcon)

new Vue({
  el: '#root',
  template: '<Root/>',
  components: { Root }
})



