<template>
  <div id="root">
    <ModalHeader :title="'Math'"></ModalHeader>
    <div class="math">
      <p>The math is pretty is easy - without all the calculations, the bookmakers must make.</p>
      <p>If 100 eth is placed on team 1, and 100 eth is placed on team 2, in any case, every winner doubles up.</p>
      <p>If 100 eth is placed on team and 200 eth is placed on team 2:</p>
      <ul>
        <li>If team 1 wins, the winners triple up.</li>
        <li>If team 2 wins, the winners win 1.5x their stake.</li>
      </ul>
      <p>And so on...</p>
      <p>Actually, that is exactly what the bookmakers do - except...</p>
      <ul>
        <li>They must show a accurate odds way BEFORE the event occurs</li>
        <li>(Contrary, we transparently show the contract, the no one can modify - it will apply this logic, without knowing the result beforehand)</li>
        <li>Bookmakers start from a negative balance - by the time you play, they have already spent millions for adverts, and licenses, that let them advertise... - they must calculate an odds that lets them win back at least that money</li>

      </ul>
    </div>
  </div>
</template>

<script>
import ModalHeader from '../../../common/ModalHeader'

export default {
  name: 'Math',
  data() {
    return {

    }
  },
  props: {

  },
  methods: {
  },
  components: {
    ModalHeader
  },
  computed: {

  },
  created() {
  }
}
</script>

<style scoped>
#root {
  position: relative;
}

.math {
  font-size: 0.8rem;
}
</style>



