<template>
  <div class="short-text">
    <p>{{ firstLine }}</p>
    <p>{{ secondLine }}</p>
    <p>{{ thirdLine }}</p>

    <p id="read-more"><span @click="readMore()">
        Read more&nbsp;&nbsp;<font-awesome-icon icon="angle-double-down"></font-awesome-icon>
      </span></p>
  </div>
</template>

<script>
export default {
    name: "ShortText",
    props: {
      firstLine: String,
      secondLine: String,
      thirdLine: String
    },
    methods: {
      readMore: function () {
        this.$emit("readMore")
      }
    }
}
</script>

<style scoped>
  .short-text {
    margin-top: 15px;
    text-align: center;
  }

  .short-text p {
    font-size: 0.8rem;
    margin: 0;
    padding: 0;
    color: #555;
  }

  #read-more {
    cursor: pointer;
    font-style: italic;
    font-weight: bold;
    font-size: 0.7rem;
    margin-top: 15px;
  }

  #read-more span:hover {
    text-decoration: underline;
    color: black;
  }
</style>



