<template>
  <div class="card small-info">
    <h4>{{ title }}</h4>
    <div class="icon-outer">
      <div class="icon-inner text-warning">
        <font-awesome-icon :icon="icon"></font-awesome-icon>
      </div>
    </div>
    <p class="small-info-main">
      {{ text }}
    </p>
    <p class="link-p" @click="open(linkForward)">{{ linkText }}</p>
  </div>
</template>

<script>
    export default {
        name: "InfoCard",
        props: {
          title: String,
          icon: String,
          text: String,
          linkText: String,
          linkForward: String
        },
        methods: {
          open: function (link) {
            window.open(link)
          }
        }
    }
</script>

<style scoped>
.link-p {
  color: green;
  text-decoration: underline;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
}

.icon-outer {
  text-align: center;
}

.icon-inner {
  font-size: 4rem;
  margin: 20px;
  color: #777;
}

.small-info h4 {
  text-align: center;
}

.small-info {
  padding: 20px;
  font-size: 0.7rem;
  text-align: justify;
}

.small-info-main {
  min-height: 100px;
}
</style>



