<template>
  <div class="grand-text" id="with-bg-op">
    <div id="first-prec" class="row">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
    name: "MainContainer"
}
</script>

<style scoped>
#with-bg-op {
  position: relative;
  background-color: gold;
}

#with-bg-op:after {
  content : "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../../../static/stadium.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  opacity : 0.25;
  z-index: 10;
}

#first-prec {
  position: relative;
  z-index: 20;
}

.grand-text {
  height: 80vh;
}
</style>



