<template>
  <div id="root">
    <ApplicationRoot
      :page="page">
    </ApplicationRoot>

    <NotificationsRoot
      :messages="notifications">
    </NotificationsRoot>

    <ModalRoot
      v-if="app != null"
      :app="app">
    </ModalRoot>
  </div>
</template>

<script>
import ApplicationRoot from './application/Root'
import NotificationsRoot from './notifications/Root'
import ModalRoot from './modal/Root'

export default {
  name: 'Root',
  data() {
    return {
      app: null,
      page: "LandingPage",
      notifications: []
    }
  },
  methods: {
    closeApp: function() {
      this.app = null
    },
    delMsg: function(id) {
      this.notifications = this.notifications.filter(msg => msg.id !== id)
    },
    addMsg: function(msg) {
      this.notifications.push(msg)
    },
    loadAccount: function () {
      this.$STORE.account.address = this.$match.getMyAddress() || 0x0
      return this.$match.getMyBalance()
        .then(balance => this.$STORE.account.balance = balance)
        .catch(() => this.$addMsg("Log in to Metamask"))
    },
    loadData: function() {
      return Promise.all([
        this.loadAccount(),
        this.$match.totalPotForHomeTeam().then(pot => this.$STORE.match.totalPotForHomeTeam = pot),
        this.$match.totalPotForAwayTeam().then(pot => this.$STORE.match.totalPotForAwayTeam = pot)
      ]).then(() => this.$ACTION.$emit("Match loaded")).catch(err => this.$addMsg("Metamask needs to be installed"))
    },
    checkNetwork: function() {
      this.$match.getNetwork()
        .then(id => this.$addMsg(id == 1 ? "Connected to the main net" : "Connect to main net in Metamask"))
        .catch(err => this.$addMsg("Could not connect to network - check Metamask"))
    },
    initListeners: function () {
      this.$match.listenToBetReport((data) => {
        let amount = web3.fromWei(data.args.value, "ether")
        let team = data.args.team ? this.$STORE.getHomeTeam() : this.$STORE.getAwayTeam()
        this.$addMsg(amount + " ETH placed on " + team)
        this.loadData()
      })
    },
  },
  components: {
    ApplicationRoot,
    NotificationsRoot,
    ModalRoot
  },
  created() {
    this.$ACTION.$on('App selected', app => this.app = app)
    this.$ACTION.$on('App closed', () => setTimeout(this.closeApp, 450))
    this.$ACTION.$on('Page selected', page => this.page = page)
    this.$ACTION.$on('Notification added', msg => this.addMsg(msg))
    this.$ACTION.$on('Notification deleted', id => this.delMsg(id))
    this.$ACTION.$on('Reload Data', () => this.loadData())

    try {
      this.loadData()
      this.initListeners()
      this.checkNetwork()
    } catch (e) {
      setTimeout(() => this.$addMsg("Metamask needs to be installed"), 1500)
    }
  }
}
</script>

<style>
#root {

}
</style>



