import Web3 from 'web3'
import Contract from 'truffle-contract'

import WorldCupFinalArtifact from '../../../chain/build/contracts/BetOnWorldCupFinal.json'

export default class Blockchain {
  constructor() {
    if (typeof web3 !== 'undefined') {
      this.web3ProviderInstance = web3.currentProvider;
      this.web3Instance = new Web3(this.web3ProviderInstance);
      this.worldCupFinalContract = Contract(WorldCupFinalArtifact);
      this.worldCupFinalContract.setProvider(this.web3ProviderInstance);
    } else {
      //this.web3ProviderInstance = new Web3.providers.HttpProvider('http://localhost:7545');
      //this.web3Instance = new Web3(this.web3ProviderInstance);
    }
  }

  match() {
    return this.worldCupFinalContract.deployed()
  }

}



