<template>
  <div class="card">
    <div class="card-header">
      <div :class="color">
        <font-awesome-icon :icon="sign"></font-awesome-icon>
        &nbsp;&nbsp;{{ title }}
        <span class="fr">
          <font-awesome-icon :icon="icon"></font-awesome-icon>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Reason',
  props: {
    title: String,
    icon: String,
    type: String
  },
  computed: {
    sign: function() {
      return this.type == '+' ? 'sign-in-alt' : 'sign-out-alt'
    },
    color: function() {
      return {
        'text-danger': this.type == '-',
        'text-success': this.type == '+'
      }
    }
  }
}
</script>

<style scoped>
.card-header {
  font-size: 1rem;
}

.card {
  margin: 20px;
}

.fr {
  float: right;
  color: #555;
}
</style>



