<template>
  <div id="why-us">
    <h5>Why us?</h5>
    <p>Check what your stakes are used to.</p>
    <hr>
    <h6>Bookmakers</h6>
    <Reason :icon="'money-check-alt'" :title="'Cover the profit of winners'" :type="'+'"></Reason>
    <Reason :icon="'university'" :title="'Burocracy and infrastructure'" :type="'-'"></Reason>
    <Reason :icon="'tv'" :title="'Adverts and marketing'" :type="'-'"></Reason>
    <Reason :icon="'piggy-bank'" :title="'Their profit'" :type="'-'"></Reason>
    <hr>
    <h6>This platform</h6>
    <Reason :icon="'money-check-alt'" :title="'Cover the profit of winners'" :type="'+'"></Reason>
    <Reason :icon="'piggy-bank'" :title="'And ours'" :type="'-'"></Reason>
    <div id="last">
      <p>
        On this platform the base of trust is the incorruptable blockchain of ethereum.
        The base of trusts here are not audits or licenses.
        Our transparent presence on github and etherscan lets anyone to inspect every bit of our source code.
        We do not operate server parks and administrate databases.
        Every bet made lives in a smart contract, visibly, yet anonymusly for everybody.
        This is the future of sports betting, casinos and in an even broader sense - money.
      </p>
    </div>
  </div>
</template>

<script>
import Reason from './Reason'
export default {
  name: 'WhyUs',
  components: {
    Reason
  }
}
</script>

<style scoped>
#why-us {
  color: #555;
}

#why-us p {
  margin: 0;
  padding: 0;
  font-size: 0.9rem;
  text-align: justify;
}

#last {
  padding-top: 20px;
}
</style>



