<template>
  <div id="notifications">
    <NotificationMessage
      v-for="msg in messages"
      :key="msg.id"
      :bus="bus"
      :id="msg.id"
      :type="msg.type"
      :text="msg.text">
    </NotificationMessage>
  </div>
</template>

<script>
import Vue from 'vue'
import NotificationMessage from './NotificationMessage'

export default {
  name: 'NotificationsRoot',
  data() {
    return {
      bus: new Vue({})
    }
  },
  props: {
    messages: Array
  },
  components: {
    NotificationMessage
  }
}
</script>

<style scoped>
#notifications {
  position: fixed;
  z-index: 200;
  top: 65px;
  right: 65px;
  width: 300px;
}
</style>



