<template lang="html">
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-4 withdraw">
        <button id="withdraw" type="button" class="btn btn-primary" @click="$match.withdrawWinnings()">
          Withdraw my rewards
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Withdraw"
}
</script>

<style lang="css">
#withdraw {
  font-size: 1.5rem;
}

.withdraw {
  text-align: center;
}
</style>



