<template>
  <div :class="compClass">
    <div v-if="!pending" id="bet-button" @click="bet()" class="disabled-bet bg-success">
      <input type="number" v-model='amount' v-on:click.stop id="inside" min="0.01" step="0.01">
      <span>ether&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
      <font-awesome-icon icon="play"></font-awesome-icon>
    </div>
    <div v-if="pending" id="bet-button" @click="bet()" class="disabled-bet bg-success">
      <div class="tc">
        <span>Pending...&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <font-awesome-icon icon="spinner" spin></font-awesome-icon>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: "BetButton",
    data() {
      return {
        amount: 0.01
      }
    },
    props: {
      bool: Boolean
    },
    methods: {
      bet: function () {
        if (this.$appStatus('beforeBet')) {
          this.$addMsg("Coming soon!")
          return
        };
        if (this.$STORE.getMyAddress() == 0x0) {
          this.$addMsg("Login with Metamask!")
          return
        }
        if (this.amount == 0) {
          this.$addMsg("Set the amount first!")
          return
        }
        if (this.$STORE.account.balance < this.amount) {
          this.$addMsg("You do not have enough eth to continue!")
          return
        }
        if (this.bool) {this.$STORE.pending.home = true} else {this.$STORE.pending.away = true}
        this.$match.bet(this.bool, this.amount)
        .then(res => this.$addMsg("Your transaction is mined."))
        .catch(err => this.$addMsg("Your bet was not placed. No payment made."))
        .then(() => {
          if (this.bool) {this.$STORE.pending.home = false} else {this.$STORE.pending.away = false}
          this.amount = 0.01
        })
      }
    },
    computed: {
      pending() {
        return this.bool ? this.$STORE.hasPendingHome() : this.$STORE.hasPendingAway()
      },
      compClass() {
         return { 'hide': this.$appStatus('beforeBet') || this.pending }
       }
    }
}
</script>

<style scoped>
.hide {
  opacity: 0.5;
  cursor: not-allowed;
}

#inside {
  border: 0;
  color: black;
  background-color: white;
  text-align: right;
  width: 50px;
  border-radius: 3px;
}

#inside:hover {
  background-color: white !important;
  color: black !important;
}

#inside:active {
  outline: 0;
  background-color: white !important;
  color: black !important;
}

.tc {
  text-align: center;
}

span {
  font-style: italic;
}

#inside:focus {
  outline: 0;
  background-color: white !important;
  color: black !important;
}

#bet-button {
  padding: 7px;
  margin: 10px;
  border-radius: 3px;
  color: white;
  cursor: pointer;
  box-shadow: 0 0 5px #555;
}

#bet-button:hover {
  box-shadow: 0 0 1px #555;
}

#bet-button:hover input {
  background-color: white;
  border: 0;
  border-radius: 3px;
  color: black;
}
</style>



