<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12 col-lg-6">
        <WhyUs></WhyUs>
      </div>

      <div class="col-md-12 col-lg-6">
        <div class="row">
          <div class="col-sm-12 col-md-6">
            <InfoCard
              :title="'Get Ether'"
              :icon="'hand-holding-usd'"
              :text="'Ether is a cryptocurrency, and while Bitcoin has the most hype, Ethereum has the best ecosystem, and truly is the cryptocurrency of the future.'"
              :linkText="'Get started with crypto'"
              :linkForward="'https://www.coinbase.com/'">
            </InfoCard>
          </div>
          <div class="col-sm-12 col-md-6">
            <InfoCard
              :title="'Get Metamask'"
              :icon="'door-open'"
              :text="'Metamask is an extension for your browser like adblock or grammarly. It lets your browser connect to the ethereum blockchain, the internet of tomorrow.'"
              :linkText="'Connect to the internet of future'"
              :linkForward="'https://metamask.io/'">
            </InfoCard>
          </div>
        </div>

        <div class="row">
          <div class="col-sm-12 col-md-6">
            <InfoCard
              :title="'Get an account'"
              :icon="'user-secret'"
              :text="'Getting an account for this app is super easy with Metamask - it is already created, and you can use it for every blockchain based, decentralized site.'"
              :linkText="'Learn more about vaults'"
              :linkForward="'forward'">
            </InfoCard>
          </div>
          <div class="col-sm-12 col-md-6">
            <InfoCard
              :title="'Learn more'"
              :icon="'info-circle'"
              :text="'Read more about the blockchain and Ethereum. It is probably the best investment for the future - learn why does it have more potential than bitcoin, or as a matter of fact any FIAT currency...'"
              :linkText="'About crypto'"
              :linkForward="'forward'">
            </InfoCard>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InfoCard from '../../../common/InfoCard'
import WhyUs from './WhyUs'

export default {
    name: "Info",
    data() {
      return {

      }
    },
    methods: {

    },
    components: {
      InfoCard,
      WhyUs
    }
}
</script>

<style scoped>
.container {
  margin-top: 50px;
  margin-bottom: 50px;
}

.col-lg-6 {
  margin-bottom: 50px;
}

.col-md-6 {
  margin-bottom: 50px;
}
</style>



