<template>
  <div id="root">
    <MainContainer>
      <Account></Account>
      <div class="container" id="middle">
        <div class="row">
          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-7">
                <div class="my-card">
                  <div class="my-header">
                    <span class="beton">Bet on Portugal for</span>
                    <span class="odds" @click="$openApp('Math')">~1.87x</span>
                  </div>
                  <hr>
                  <div class="my-body">
                    <Flag :flags="['brasil']" :flow="'up'"></Flag>
                    <hr>
                    <BetButton :bool="true" v-if="$appStatus('betPeriod') || $appStatus('beforeBet')"></BetButton>
                    <Withdraw v-if="$appStatus('withdrawalPeriod')"></Withdraw>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-9">
                <img src="../../../../../static/favicon.png" id="worldcupimage">
              </div>
            </div>
          </div>

          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-7">
                <div class="my-card">
                  <div class="my-header">
                    <span class="beton">Bet on Brasil for</span>
                    <span class="odds" @click="$openApp('Math')">~2.07x</span>
                  </div>
                  <hr>
                  <div class="my-body">
                    <Flag :flags="['england']" :flow="'down'"></Flag>
                    <hr>
                    <BetButton :bool="false" v-if="$appStatus('betPeriod') || $appStatus('beforeBet')"></BetButton>
                    <Withdraw v-if="$appStatus('withdrawalPeriod')"></Withdraw>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Countdown v-if="$appStatus('beforeBet')"></Countdown>
    </MainContainer>

    <ShortText
      @readMore="slide()"
      :firstLine="'Use your ether to bet on the world cup finel here, with the best possible odds, with any amount you wish to, with just one click.'"
      :secondLine="'This bet lives in an incorruptable smart contract on the ethereum blockchain transparently and anonymously.'"
      :thirdLine="'Play now, or collect some more info before that.'">
    </ShortText>

    <Info></Info>

    <Footer></Footer>
  </div>
</template>

<script>
import MainContainer from '../../../common/MainContainer'
import ShortText from '../../../common/ShortText'
import Footer from '../../../common/Footer'
import Flag from './Flag'
import Info from './Info'
import BetButton from './BetButton'
import Account from './Account'
import Countdown from './Countdown'
import Withdraw from './Withdraw'

export default {
  name: 'LandingPage',
  components: {
    MainContainer,
    ShortText,
    Info,
    Flag,
    BetButton,
    Account,
    Countdown,
    Footer,
    Withdraw
  },
  data() {
    return {
      homePlayed: false,
      awayPlayed: false
    }
  },
  methods: {
    slide: function () {
      window.scrollTo({top: 650, behavior: "smooth"})
    }
  }
}
</script>

<style scoped>
#middle {
  margin-top: 120px;
}

#worldcupimage {
  max-width: 200px;
}

.col-md-7 {
  font-size: 0.8rem;
}

.my-header {
  display: flex;
  justify-content: space-between;
  color: black;
  width: 100%;
  padding: 10px;
  padding-bottom: 0;
}

.my-body {
  padding: 10px;
}

.beton {
  margin: 0;
  padding: 0;
}

.odds {
  margin: 0;
  padding: 0;
  font-weight: bold;
  cursor: pointer;
}

.odds:hover {
  text-decoration: underline;
}

.my-card {
  padding: 0;
  background-color: rgba(255,215,0,0.4);
  border-radius: 3px;
  box-shadow: 0 0 15px #555;
  transition: 0.7s;
}

.my-card:hover {
  background-color: rgba(255,215,0,0.8);
}

.my-card:hover {
  opacity: 1;
}

.hoverable {
  opacity: 0.7;
}

.hoverable:hover {
  opacity: 1;
}
</style>



