import Vue from 'vue'

export default class ControllerPlugin {

  static ACTION = new Vue({
    data: {
      notificationCounter: 0,
      status: "betPeriod" // beforeBet betPeriod, duringMatch, withdrawalPeriod
    },
    methods: {
      appStatus: function(status) {
        return this.status == status
      },
      redirectTo: function(page) {
        this.$emit("Page selected", page)
      },
      openApp: function(app) {
        this.$emit("App selected", app)
      },
      closeApp: function() {
        this.$emit("App closed")
      },
      addMsg: function(text) {
        let id = this.notificationCounter
        this.notificationCounter++
        this.$emit("Notification added", {id, text})
      },
      delMsg: function(id) {
        this.$emit("Notification deleted", id)
      }
    }
  })

  static install(Vue, options) {
   Vue.prototype.$redirectTo = ControllerPlugin.ACTION.redirectTo
   Vue.prototype.$openApp = ControllerPlugin.ACTION.openApp
   Vue.prototype.$closeApp = ControllerPlugin.ACTION.closeApp
   Vue.prototype.$addMsg = ControllerPlugin.ACTION.addMsg
   Vue.prototype.$delMsg = ControllerPlugin.ACTION.delMsg
   Vue.prototype.$ACTION = ControllerPlugin.ACTION

   Vue.prototype.$appStatus = ControllerPlugin.ACTION.appStatus
  }

}



